
import { formatDate } from "@/common/js/util"
import { callPuExctQryToOICS } from "@/api/api"
import OFooter from "@/components/common/OFooter"
import OUploader from "@/components/common/OUploader"
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
Group, XInput, XTextarea, XButton, ViewBox, Checklist, OFooter, OUploader, AlertModule, Loading
},
data() {
return {
siteInspectionItem: {},//存入localStorage的一条现场检查待办任务数据
RSLTRESULTList: [{ key: '11', value: '有问题' }, { key: '21', value: '无问题' }, { key: '31', value: '无此业务' }],//检查结论选择项
RSLTRESULT: [],//检查结论 11-有问题 21-无问题 31-无此业务
propUploaderTitle: '上传图片',//图片控件标题
propLableStyle: { width: '4.5em', textAlign: 'justify', marginRight: '1.5em' },//图片控件标题样式
propMaxCount: 10,//上传图片最大数
uploadList: [],////初始化，选择的图片列表，待上传至服务器
propSuccessImgList: [],//初始化，已经上传成功的图片列表，接口返回数据
disabledButton: false,
reqUploadList: [],//需要执行批次上传的图片集合，现在每次上传2张
pageIndex: 0,//查询图片当前页
pageSize: 2,//查询图片每页数量
loadingShow: false,
loadingText: '请求中',
imgMax: 0,//待上传的图片总数
imgCount: 0,//上传成功的图片数量
isDefault: false,//是否显示图片上传、加载进度
}
}, created() {

},
methods: {

getImageItems() {
if (this.loadingShow == false) {
return
}
this.pageIndex++
let params = {
params: {
RuleName: "GetImageItems",
Parm: {
CaseNo: this.siteInspectionItem.EXECUTEKEY + "_" + this.siteInspectionItem.RSLTKEY
, PageSize: this.pageSize
, PageIndex: this.pageIndex
}
}
};
let callBackSuccess = data => {
if (data.ReturnCd == '00') {
this.propSuccessImgList = this.propSuccessImgList.concat(data.RspMsg.result.ListItems.ListItem || []);
setTimeout(() => {
this.imgMax = data.RspMsg.total
this.imgCount = this.imgCount + data.RspMsg.result.ListItems.ListItem.length
if (!data.RspMsg.result.ListItems.ListItem || this.imgCount == data.RspMsg.total || data.RspMsg.result.ListItems.ListItem.length != this.pageSize) {
this.loadingShow = false
}
else {
this.getImageItems()
}
}, 10)
}
else {
this.loadingShow = false
this.showModule("获取检查影像失败", data.RspMsg.msg);
}
};
let callBackError = data => {
this.loadingShow = false
this.showToast('获取检查影像失败', '20.0em', 'warn')
};
callPuExctQryToOICS(params, callBackSuccess, callBackError);
},//弹出信息提示窗
showModule(title, content) {
AlertModule.show({
title: title,
content: content,
onShow() {

},
onHide() {

}
})
}, showToast(text, width, type) {
this.$vux.toast.show({
text: text,
type: type,//success, warn, cancel, text
width: width
})
},//获取上传的图片信息
getUploadList(uploadList) {
this.uploadList = uploadList

},

handleSiteInspection(WorkState) {


if (this.RSLTRESULT.length <= 0) { this.showToast('请选择检查结论', '8.0em', 'cancel'); return }

if (this.RSLTRESULT[0] == "11") {
if (this.siteInspectionItem.DESCR == '') { this.showToast('请输入检查描述', '8.0em', 'cancel'); return }
}

this.loadingText = '请求中'
this.loadingShow = true
this.isDefault = false
this.disabledButton = true
const _this = this
let params = {
params: {
RuleName: "HandleSiteInspection",
Parm: {
TimePoint: formatDate(new Date(), 'yyyyMMdd')
, EXECUTEKEY: this.siteInspectionItem.EXECUTEKEY
, FRSLTKEY: this.siteInspectionItem.FRSLTKEY
, RSLTKEY: this.siteInspectionItem.RSLTKEY
, RSLTRESULT: this.RSLTRESULT[0]
, DESCR: this.siteInspectionItem.DESCR
, WorkState: WorkState
}
}
};
let callBackSuccess = data => {

if (data.ReturnCd == '00' && data.RspMsg.result[0].DataFlag == '01') {










if (this.$refs.uploader.addImgCount > 0) {


for (let i = 0; i < this.uploadList.length; i++) {
let item = this.uploadList[i]
if (item.ImageId < 0) {
this.reqUploadList.push({ ImageId: item.ImageId, FileName: item.FileName, FileContent: item.src.slice(23) })
}
}
this.imgMax = this.reqUploadList.length
this.imgCount = 0

this.handleImageItems()
}
else {

this.loadingShow = false
_this.$vux.toast.show({
text: '保存成功',
width: '6em',
onHide() {
_this.localStorageRemoveItem()
_this.$router.go(-1)
}
})
}

if (this.$refs.uploader.deleteImgList.length > 0) { this.deleteHandleImageItems() }
}
else {
this.loadingShow = false
this.showModule("保存失败", data.RspMsg.msg);
this.disabledButton = false
}
};
let callBackError = data => {
this.loadingShow = false
this.showModule("保存失败", data.RspMsg.msg);
this.disabledButton = false
};
callPuExctQryToOICS(params, callBackSuccess, callBackError);
},//返回上一页
onBack() {
this.localStorageRemoveItem()
},//返回主页
onHomeMenu() {
this.localStorageRemoveItem()
},//移除localStorage数据
localStorageRemoveItem() {
localStorage.removeItem("SiteInspectionItem")
},//上传图片
handleImageItems() {


const _this = this
let imgList = this.reqUploadList.splice(0, 2)
this.loadingText = "上传图片中"
this.isDefault = true
this.imgCount = this.imgCount + imgList.length
let params = {
params: {
RuleName: "HandleImageItems",
Parm: {
CaseNo: this.siteInspectionItem.EXECUTEKEY + "_" + this.siteInspectionItem.RSLTKEY,//现场检查CaseNo为：EXECUTEKEY+"_"+RSLTKEY
HandleState: '02'
, ListItems: {
ListItem: imgList
}
}
}
};
let callBackSuccess = data => {

if (data.ReturnCd == '00' && data.RspMsg.result[0].DataFlag == '01') {
if (this.reqUploadList.length > 0) {
this.handleImageItems()
}
else {

this.loadingShow = false
_this.$vux.toast.show({
text: '保存成功',
width: '6em',
onHide() {
_this.localStorageRemoveItem()
_this.$router.go(-1)
}
})
}
}
else {
this.loadingShow = false
this.showModule('上传图片失败', data.RspMsg.msg);

}
};
let callBackError = data => {
this.loadingShow = false
this.showToast('上传图片失败', '20.0em', 'warn')

};
callPuExctQryToOICS(params, callBackSuccess, callBackError);
},//删除图片
deleteHandleImageItems() {
let params = {
params: {
RuleName: "HandleImageItems",
Parm: {
CaseNo: this.siteInspectionItem.EXECUTEKEY + "_" + this.siteInspectionItem.RSLTKEY,//现场检查CaseNo为：EXECUTEKEY+"_"+RSLTKEY
HandleState: '04'
, ListItems: {
ListItem: this.$refs.uploader.deleteImgList
}
}
}
};
let callBackSuccess = data => {

if (data.ReturnCd == '00' && data.RspMsg.result[0].DataFlag == '01') {

}
else {
_this.showModule('删除图片失败', data.RspMsg.msg);
}
};
let callBackError = data => {
this.loadingShow = false
this.showToast('删除图片失败', '20.0em', 'warn')

};
callPuExctQryToOICS(params, callBackSuccess, callBackError);
},
getImgCount() {
return this.imgCount + '/' + this.imgMax
}
},
mounted() {
},
beforeRouteEnter(to, from, next) {
next(vm => {
vm.loadingText = '加载中'
vm.loadingShow = true
vm.isDefault = false
vm.uploadList = []
vm.propSuccessImgList = []
vm.RSLTRESULT = []
vm.disabledButton = false
vm.pageIndex = 0
vm.$refs.uploader.addImgCount = 0
vm.reqUploadList = []
vm.$refs.uploader.deleteImgList = []
vm.imgMax = 0
vm.imgCount = 0
vm.siteInspectionItem = JSON.parse(localStorage.getItem("SiteInspectionItem"));
if (vm.siteInspectionItem.RSLTRESULT != "") vm.RSLTRESULT.push(vm.siteInspectionItem.RSLTRESULT)

if (vm.siteInspectionItem.FLAG == '1') {
vm.loadingText = "图片加载中"
vm.isDefault = true
vm.getImageItems()
}
else {
vm.loadingShow = false
}
})
},//控制浏览大图不返回
beforeRouteLeave(to, from, next) {
let isClickImg = window.sessionStorage.getItem("isClickImg")
if (isClickImg == '0') {
this.loadingShow = false
next()
}
else {
this.$refs.uploader.$refs.previewer.close()
next(false)
}
}
}



